html{
    scroll-behavior: smooth;
    overflow-x: hidden;
}

body{
    padding: 0;
    margin: 0;
    width: 100%;
    height: 100%;
    background-color: #3a3a3a;
    color: rgb(222, 222, 222);
}

.colbox{
    display: flex;
    flex-direction: row;
}

.rowbox{
    display: flex;
    flex-direction: column;
}

#header{
    height: 500px;
    background:linear-gradient(to bottom,#111111 ,transparent);
    position: relative;
}

.section{
    opacity: 0;
    width: 100%;
    transition: .5s;
    position: relative;
    padding-top: 30px;
    transform: translateY(50px);
}

.content{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    line-height: 27px;
    font-size: 30px;
    text-align: left;
    margin-left: 50px;
    flex-grow: 1;
}

.center{
    display: flex;
    align-items: center;
    flex-direction: column;
}

.active{
    opacity: 1;
    transform: none;
}

.subtitle{
    flex-grow: 1;
    position: relative; 
}

.marker{
    opacity: 0;
    cursor: pointer;
    transition: .5s;
    margin-right: 7px;
    text-decoration: none;
    color: rgb(138, 138, 138);
    position: absolute;
    left: 7px;
    top: 3px;
    font-size: 20px;
}

#footer{
    height: 100px;
}

.footer{
    margin-left: 50px;
    margin-right: 50%;
    font-size: 14px;
    font-weight: 100;
}